import React, {useEffect, useState} from 'react';
import {Link, useLocation, useNavigate} from 'react-router-dom';
import axios from "axios";
function Mainauction(props) {
    const [lst,setList]=useState([]);// 
    const navigate=useNavigate();
    const [message, setMessage] = useState("");
    const location = useLocation();

    const logincheck = async (roomId) =>{
        try {
            await axios.get('/lobby/logincheck');
            navigate(`/room/${roomId}`);
        } catch (error) {
            alert(' .');
            navigate('/login');
        }
    };
    useEffect(()=>{
        fetch('/lobby/list') // url http  url   promise 
            .then(res=>res.json())
            .then(res=>{
                setList(res);
            });
    },[]);
    const RoomCreate=(e)=>{ //  
        let name=prompt(' ').trim();
        if(name.length===0)
            return; //  
        //    
        // fetch 

        // 
         fetch('/lobby/create',{
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                name
            })
        })//post option  
        // if (response.status === 401) {
        //     alert('    .');
        //     return;
        // }
        //
        // if (!response.ok) {
        //     alert('  .  .');
        //     return;
        // }
        // const res = await response.json();
        // if (res === null) {
        //     alert('  .  .');
        //     return;
        // }
             .then(res=>res.json())
             .then(res=>{
                 setList([
                     res,
                     ...lst
                 ])
             })
    }
    const [emailss, setEmail] = useState('');


    return (
        <div>
            <button onClick={RoomCreate} style={{backgroundColor:'yellow'}}
                    ></button>
            <hr/>
            <ul>
                {
                    lst.map((item,idx)=>{
                        return (
                            <li key={idx} onClick={() => logincheck(item.roomId)} style={{cursor:'pointer'}}>
                                {idx+1}. {item.roomName}
                            </li>
                        );
                    })
                }
            </ul>
        </div>
    );
}
export default Mainauction;