import "../css/bidmodal.css";
import { useEffect, useState } from "react";
import axios from "axios";

const BidModal = ({ onClose }) => {
    const [userdata, setUserdata] = useState("");
    const [price, setPrice] = useState("");

    useEffect(() => {
        axios
            .get("/room/userdata")
            .then((response) => {
                setUserdata(response.data);
            })
            .catch((error) => {
                console.error(" !", error);
            });
    }, []);

    const handleInputChange = (e) => {
        setPrice(e.target.value);
    };

    const bidHandler = async (e) => {
        e.preventDefault();
        try {
            const bid_data = await axios.post(
                "/product/price-compare",
                {
                    userId: userdata.email,
                    price: parseInt(price),
                    productName: "1"
                },
                {
                    params: {
                        productName: "1"
                    },
                }
            );
            console.log(bid_data);
        } catch (err) {
            console.error("   !", err);
        }
    };

    return (
        <div className="div4">
            <div className="please-make-a">{`Please make a bid `}</div>
            <div className="price">price</div>
            <div className="child">
                <input
                    width="60%"
                    type={"number"}
                    min="0"
                    step="100"
                    pattern="[0-9]*"
                    onChange={handleInputChange}
                />
            </div>
            <div className="item">
                <button className="bidding" onClick={bidHandler}>
                    Bidding
                </button>
                &nbsp;&nbsp;&nbsp;
                <button className="close-button" onClick={onClose}>
                    Close
                </button>
            </div>
        </div>
    );
};

export default BidModal;
